import React, { Component, Fragment } from 'react';
import './App.css';
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.bundle.min";
import Home from './components/Home/Home';
import Projects from './components/Projects/Projects';
import WorkEx from './components/WorkEx/WorkEx';
import Pose from './components/Pose'
import Footer from './components/Footer/Footer';
import Blog from './components/Blog/Blog'
import {
	BrowserRouter as Router,
	// HashRouter as Router,
	Route,
	Switch,
} from "react-router-dom";
import Navbar from './components/Navbar/Navbar';
import PathFinder from './components/Experiment/PathFinder';
import IntereactiveProject from "./components/InteractiveProjects/InteractiveProjects";

const MainScreen = () => {
	return (
		<Fragment>
			<Navbar />
			<Home />
			<WorkEx />
			<Projects />
			<IntereactiveProject />
			<Blog />
		</Fragment>
	)
}

export class App extends Component {
	render() {
		return (
			<div className="app">
				<Router>
					<Route path="/" exact component={MainScreen} />
					<Route path="/maze" exact component={PathFinder} />
					<Footer />
				</Router>
			</div>
		);
	}
}

export default App

