import React, { Component } from 'react';
import "./Home.css";
import Fade from 'react-reveal/Fade';
import Typist from "react-typist";
import profileIcon from '../../media/profile.png';
import Particles from 'react-particles-js';
import { callbackify } from 'util';

interface IState {
    mobileDevice: boolean
}

export default class Home extends Component<{}, IState> {
    constructor(props) {
        super(props)

        this.state = {
            mobileDevice: false
        }
    }

    componentDidMount() {
        window.addEventListener('resize', () => {
            if (window.innerWidth < window.innerHeight) {
                this.setState({
                    mobileDevice: true
                });
            } else {
                this.setState({
                    mobileDevice: false
                });
            }
        });
    }

    render() {
        return (
            <div
                id="home"
                className="home-body title bg-transparent bgstyle min-vh-100 align-content-center align-items-center m-0"
            >
                <Particles
                    className="particles"
                    params={{
                        "particles": {
                            "number": {
                                "value": 160,
                                "density": {
                                    "enable": false
                                }
                            },
                            "size": {
                                "value": 3,
                                "random": true,
                                "anim": {
                                    "speed": 4,
                                    "size_min": 0.3
                                }
                            },
                            "line_linked": {
                                "enable": false
                            },
                            "move": {
                                "random": true,
                                "speed": 1,
                                "direction": "top",
                                "out_mode": "out"
                            }
                        },
                        "interactivity": {
                            "events": {
                                "onhover": {
                                    "enable": true,
                                    "mode": "bubble"
                                },
                                "onclick": {
                                    "enable": true,
                                    "mode": "repulse"
                                }
                            },
                            "modes": {
                                "bubble": {
                                    "distance": 250,
                                    "duration": 2,
                                    "size": 0,
                                    "opacity": 0
                                },
                                "repulse": {
                                    "distance": 400,
                                    "duration": 4
                                }
                            }
                        }
                    }}

                    height="100vh"
                    width="100%"

                    style={
                        {
                            background: "#3d4c5c"
                        }
                    }
                />
                <div className="container container-fluid text-center"
                    style={{
                        top: window.innerWidth < window.innerHeight ? "10vh" : "30vh",
                        position: "absolute",
                        zIndex: 999,
                        left: window.innerWidth < window.innerHeight ? "" : "20vw"
                    }}
                >
                    <div className="row">
                        <div className="col-md-4">
                            <img src={profileIcon} className="img-fluid" width="250" height="250" alt="Prateek Jha" />
                        </div>
                        <div className="col-md-6">
                            <p className="text-light">
                                Hello | नमस्ते
                                <br />
                                I am Prateek
                            </p>
                            <Typist className="lead text-light">
                                <span>
                                    Software Engineer @ <b><a href="https://www.odessainc.com/" target="_blank" className="odessa">Odessa</a></b>
                                    <br />
                                    <b><a className="mit" href="https://covid19challenge.mit.edu/india/" target="_blank">Winner</a></b>, ​MIT COVID-19 Challenge in Track F
                                    <br />
                                    Full Stack Developer and Machine Learning Hobbyist
                                </span>
                            </Typist>
                            <span className="lead text-light">
                                <br />
                                <a href="https://www.linkedin.com/in/prateek-kr-jha/" target="_blank" className="badge badge-light">Connect with me for Technical Collaborations.</a>
                            </span>
                        </div>
                    </div>
                </div>

            </div >
        )
    }
}