import React, { Component } from 'react';
import './GithubProjectCard.css';
import axios from 'axios';
import axiosRetry from 'axios-retry';
import LangColors from './GithubLangColors.json'

interface IProps {
    repositoryName: string,
    userName: string
}

interface RepositoryDetils {
    Name: string,
    Description: string,
    LastChange: Date,
    URL: string
}

interface IState {
    isRepoDetailsLoaded?: boolean
    isRepoLanguageDetailsLoaded?: boolean,
    repositoryDetails?: RepositoryDetils,
    repositoryLanguages?: Array<string>
}

export class GithubProjectCard extends Component<IProps, IState> {
    constructor(props: IProps) {
        super(props)

        this.state = {
            isRepoDetailsLoaded: false,
            isRepoLanguageDetailsLoaded: false
        }
    }


    fetchRepositoryDetails() {
        let repoUrl = `https://api.github.com/repos/${this.props.userName}/${this.props.repositoryName}`
        axios.get(repoUrl)
            .then(res => {
                let repositoryData = {
                    'Name': res.data.name,
                    'Description': res.data.description,
                    'LastChange': res.data.updated_at,
                    'URL': res.data.homepage !== "" ? res.data.homepage : res.data.html_url,
                };
                // console.log(repositoryData);
                this.setState({
                    isRepoDetailsLoaded: true,
                    repositoryDetails: repositoryData
                });
            })
            .catch(error => {
                console.error(`Error while fetching Repository ${this.props.repositoryName}: `, error);
            });

        axiosRetry(axios, { retryDelay: axiosRetry.exponentialDelay });
    }

    fetchLanguageDetails() {
        let repoUrl = `https://api.github.com/repos/${this.props.userName}/${this.props.repositoryName}/languages`
        axios.get(repoUrl)
            .then(res => {
                // console.log('Name', Object.keys(res.data));
                this.setState({
                    isRepoLanguageDetailsLoaded: true,
                    repositoryLanguages: Object.keys(res.data)
                });
            })
            .catch(error => {
                console.error(`Error while fetching Repository ${this.props.repositoryName}: `, error);
            });

        axiosRetry(axios, { retryDelay: axiosRetry.exponentialDelay });
    }

    componentDidMount() {
        this.fetchRepositoryDetails();
        this.fetchLanguageDetails();
    }

    render() {

        var dataToRender: any;
        if (this.state.isRepoDetailsLoaded && this.state.isRepoLanguageDetailsLoaded) {

            var langs: Array<any> = new Array<any>();
            this.state.repositoryLanguages?.forEach(language => {
                const langColor: React.CSSProperties = {
                    backgroundColor: LangColors[language].color
                };

                langs.push(
                    <li key={"lang-" + language} className="lang-row">
                        <div className="lang-color"
                            style={langColor}
                        ></div>
                        <span>
                            {language}
                        </span>
                    </li>
                )
            });

            dataToRender = (
                <div className="details card-body">
                    <div className="name">
                        <h5 className="card-title">{this.state.repositoryDetails?.Name}</h5>
                        <a href={this.state.repositoryDetails?.URL} target="_blank">
                            <i className="fa fa-external-link-square" aria-hidden="true"></i>
                        </a>
                    </div>
                    <div className="description-lang card-text">
                        <p className="desc">
                            {this.state.repositoryDetails?.Description}
                        </p>
                        <ul className="lang row">
                            {langs}
                        </ul>
                    </div>
                    <div className="active-date">
                        Last Active: {new Date(this.state.repositoryDetails?.LastChange!).toUTCString()}
                    </div>
                </div>
            )
        } else {
            dataToRender = (
                <h3>Loading...</h3>
            )
        }

        return (
            <div className="col-md-6">
                <div className="github-card">
                    {dataToRender}
                </div>
            </div>
        )
    }
}

export default GithubProjectCard
