import React, { Component } from 'react';
import { Link } from "react-router-dom";
import "./Navbar.css";

interface IState {
    showBackground: boolean
}

export default class Navbar extends Component<{}, IState> {
    constructor(props) {
        super(props)

        this.state = {
            showBackground: true
        }
    }

    componentDidMount() {
        document.addEventListener('scroll', () => {
            if (window.scrollY < 200 && window.location.pathname === '/') {
                this.setState({
                    showBackground: true
                });
            } else {
                this.setState({
                    showBackground: false
                });
            }
        });
    }

    render() {
        return (
            <nav
                className={`navbar navbar-expand-lg fixed-top navbar-light ${this.state.showBackground ? "bg-transparent navbar-dark" : "shadow-lg rounded bg-light"} `}
            >
                <Link className="navbar-brand" to="/">
                    Prateek Jha
                </Link>
                <button
                    className="navbar-toggler"
                    type="button"
                    data-toggle="collapse"
                    data-target="#navbarTogglerDemo02"
                    aria-controls="navbarTogglerDemo02"
                    aria-expanded="false"
                    aria-label="Toggle navigation"
                >
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarTogglerDemo02">
                    <ul className="navbar-nav mr-auto mt-2 mt-lg-0">
                        <li className="nav-item active" data-toggle="collapse" data-target=".navbar-collapse.show">
                            <a className="nav-link" href="/#">About</a>
                        </li>
                        <li className="nav-item active" data-toggle="collapse" data-target=".navbar-collapse.show">
                            <a className="nav-link" href="/#work">Career</a>
                        </li>
                        <li className="nav-item active" data-toggle="collapse" data-target=".navbar-collapse.show">
                            <a className="nav-link" href="/#project">Projects</a>
                        </li>
                        <li className="nav-item active" data-toggle="collapse" data-target=".navbar-collapse.show">
                            <a className="nav-link" href="/#interact">Intereactive Projects</a>
                        </li>
                        <li className="nav-item active" data-toggle="collapse" data-target=".navbar-collapse.show">
                            <a className="nav-link" href="/#blog">Blogs</a>
                        </li>
                    </ul>
                    <span>
                        <ul className="navbar-nav mr-auto mt-2 mt-lg-0">
                            <li className="nav-item active" data-toggle="collapse" data-target=".navbar-collapse.show"><a className="nav-link" href="mailto:prateekj777@gmail.com"><i className="fa fa-envelope" aria-hidden="true"></i></a></li>
                            <li className="nav-item active" data-toggle="collapse" data-target=".navbar-collapse.show"><a className="nav-link" href="https://twitter.com/_prateekjha" target="_blank"><i className="fa fa-twitter" aria-hidden="true"></i></a></li>
                            <li className="nav-item active" data-toggle="collapse" data-target=".navbar-collapse.show"><a className="nav-link" href="https://www.instagram.com/_prateekjha_" target="_blank"><i className="fa fa-instagram" aria-hidden="true"></i></a></li>
                            <li className="nav-item active" data-toggle="collapse" data-target=".navbar-collapse.show"><a className="nav-link" href="https://www.linkedin.com/in/prateek-kr-jha/" target="_blank"><i className="fa fa-linkedin" aria-hidden="true"></i></a></li>
                        </ul>
                    </span>
                </div>
            </nav>
        )
    }
}
