import React, { Component } from 'react';
import './Projects.css';
import GithubProjectCard from '../GithubProjectCard/GithubProjectCard'
import Fade from 'react-reveal/Fade';

export default class Projects extends Component {
    render() {
        const repoNames: Array<string> = ["react_posenet", "explorer", "ngo_matching", "Home", "NetWalker"];
        const userName: string = "jha-prateek";
        var repoToRender: Array<any> = [];
        repoNames.forEach((item, key) => {
            repoToRender.push(
                <GithubProjectCard key={key} userName={userName} repositoryName={item} />
            );
        });

        return (
            <div
                className="projects-grid jumbotron jumbotron-fluid bg-transparent m-0"
                id="project"
            >
                <div className="container container-fluid p-5">

                    <h1 className="display-4 pb-5">Projects</h1>
                    <div className="row">
                        <GithubProjectCard userName="Quarantine-Projects" repositoryName="easy_definition" />
                        {repoToRender}
                    </div>
                </div>
            </div>
        )
    }
}
