import React, { Component } from 'react';
import ProjectCard from './../ProjectCard/ProjectCard';

export default class InteractiveProjects extends Component {
    render() {
        return (
            <div
                className="blog jumbotron jumbotron-fluid bg-gradient-light m-0"
                id="interact"
            >
                <div className="container container-fluid p-5">

                    <h1 className="display-4 pb-5">Interact</h1>
                    <div className="row">
                        <ProjectCard link="/maze" title="Path Finder" />
                    </div>
                </div>
            </div>
        )
    }
}
