import React, { Component } from 'react';
import BlogCard from '../BlogCard/BlogCard';
import './Blog.css';
import Fade from 'react-reveal/Fade';

export class Blog extends Component {
    render() {
        return (
            <div
                className="blog jumbotron jumbotron-fluid bg-transparent m-0"
                id="blog"
            >
                <div className="container container-fluid p-5">

                    <h1 className="display-4 pb-5">Blogs</h1>
                    <div className="row">
                        <BlogCard
                            title="How to Teach Machines inside a browser?"
                            description="In recent years, the world has seen many major breakthroughs in the field of Machine Learning and so did the libraries used to write these programs. 
                        From NumPy to Tensorflow.js, each one faster and equipped with the best available technology of time than the previous one. One such library that we are going to talk about today is ml5.js. 
                        ml5 is..."
                            blogUrl="https://analyticsindiamag.com/how-to-teach-machines-inside-a-browser/"
                        />
                    </div>
                </div>
            </div>
        )
    }
}

export default Blog
