import React, { Component } from 'react';
import odessaPng from '../../media/odessa.png';
import gisclePng from '../../media/giscle.png';
import AnimationWrapper from '../AnimationWrapper';
import { VerticalTimeline, VerticalTimelineElement } from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';
import './WorkEx.css';

interface IState {
    mobileDevice: boolean
}

export default class WorkEx extends Component<{}, IState> {
    constructor(props) {
        super(props)

        this.state = {
            mobileDevice: false
        }
    }

    componentDidMount() {
        window.addEventListener('resize', () => {
            if (window.innerWidth < window.innerHeight) {
                this.setState({
                    mobileDevice: true
                });
            } else {
                this.setState({
                    mobileDevice: false
                });
            }
        });
    }

    render() {

        const imgStyle: React.CSSProperties = {
            mixBlendMode: 'multiply'
        }

        const timeLineIconStyle: React.CSSProperties = {
            marginTop: window.innerWidth < window.innerHeight ? "14px" : "24px"
        }

        return (
            <div
                id="work"
                className="work-experience jumbotron jumbotron-fluid bg-gradient-light m-0"
            >
                {/* <AnimationWrapper> */}
                <div className="container container-fluid p-5">

                    <h1 className="display-4 pb-5">Career</h1>

                    <VerticalTimeline>
                        <VerticalTimelineElement
                            className="vertical-timeline-element--work"
                            contentStyle={{ background: 'rgb(247, 247, 247)', borderRadius: '5px' }}
                            contentArrowStyle={{ borderRight: '7px solid  rgb(112, 112, 112)' }}
                            date="May 2019 - Present"
                            iconStyle={{ background: 'rgb(217, 217, 217)' }}
                            icon={<i className="fa fa-briefcase" style={timeLineIconStyle} aria-hidden="true"></i>}
                        >
                            <h4 className="vertical-timeline-element-title">Software Engineer <a href="https://www.odessainc.com/" target="_blank" rel="noopener noreferrer">
                                <img src={odessaPng} alt="Odessa Inc." width="50px" height="15px" /></a></h4>
                            <p>I work on LeaseWave, an end to end browser-based lease and loan management system.
                                My work is to design new features and enhance the existing product as per the request from clients.</p>
                            <p>Projects include the addition of new modules like Direct Debit Mandate for
                            European Market, Fleet Maintenance module, Pool Syndication for syndicating leases in
                            bulk.</p>
                        </VerticalTimelineElement>
                        <VerticalTimelineElement
                            className="vertical-timeline-element--work"
                            contentStyle={{ background: 'rgb(247, 247, 247)', borderRadius: '5px' }}
                            contentArrowStyle={{ borderRight: '7px solid  rgb(112, 112, 112)' }}
                            date="Dec 2018 -May 2019"
                            iconStyle={{ background: 'rgb(217, 217, 217)' }}
                            icon={<i className="fa fa-briefcase" style={timeLineIconStyle} aria-hidden="true"></i>}
                        >
                            <h4 className="vertical-timeline-element-title">Software Engineer Trainee <a href="https://www.odessainc.com/" target="_blank" rel="noopener noreferrer">
                                <img src={odessaPng} alt="Odessa Inc." width="50px" height="15px" /></a></h4>
                            <p>Training on .Net Framework, Entity Framework, and MS SQL Server.</p>
                            <p>Developed a mock-CRM module on top of Leasewave as part of the training process.
                                Bug-fixing and performance as well as feature enhancements in the existing product as per request from the clients.</p>
                        </VerticalTimelineElement>
                        <VerticalTimelineElement
                            className="vertical-timeline-element--work"
                            contentStyle={{ background: 'rgb(247, 247, 247)', borderRadius: '5px' }}
                            contentArrowStyle={{ borderRight: '7px solid  rgb(112, 112, 112)' }}
                            date="Aug 2020 - Sept 2020"
                            iconStyle={{ background: 'rgb(217, 217, 217)' }}
                            icon={<i className="fa fa-briefcase" style={timeLineIconStyle} aria-hidden="true"></i>}
                        >
                            <h4 className="vertical-timeline-element-title">Developer (Contract)</h4>
                            <p>I developed a web application to document the medical information of all the staff and visitors entering the premises.</p>
                            <p>The web app had features like an interactive
                            dashboard with advanced search filters, save data to the workbook/excel, user
                            authentication and authorization for members to securely fill in their details, etc.</p>
                            <p><b>Stack:</b> Django, ReactJS, PostgreSQL, Docker, Nginx</p>
                        </VerticalTimelineElement>
                        <VerticalTimelineElement
                            className="vertical-timeline-element--work"
                            contentStyle={{ background: 'rgb(247, 247, 247)', borderRadius: '5px' }}
                            contentArrowStyle={{ borderRight: '7px solid  rgb(112, 112, 112)' }}
                            date="July 2018 - Sept 2018"
                            iconStyle={{ background: 'rgb(217, 217, 217)' }}
                            icon={<i className="fa fa-briefcase" style={timeLineIconStyle} aria-hidden="true"></i>}
                        >
                            <h4 className="vertical-timeline-element-title">Deep Learning Intern <a href="https://www.linkedin.com/company/giscle/" target="_blank" rel="noopener noreferrer">
                                <img src={gisclePng} alt="Giscle Sysyems" width="50px" height="15px" style={imgStyle} /></a></h4>
                            <p>My task was to develop a Face Recognition prototype for the clients and integrate this feature in the form of Rest-API to their existing product.
                                I got an opportunity to work on various Deep Learning models such as ResNet, MTCNN, YOLO, OpenFace, DLib, etc.</p>
                        </VerticalTimelineElement>
                    </VerticalTimeline>
                </div>
                {/* </AnimationWrapper> */}
            </div>
        )
    }
}
