import React, { Component } from 'react';
import './PathFinder.css';
import { Link } from "react-router-dom";

class Node {
    i: number;
    j: number;
    f: number;
    g: number;
    h: number;
    neighbours: Array<Node>;
    previous: Node | null;
    obstacle: boolean;

    constructor(x: number, y: number, wallRandomNess: number) {
        this.i = x;
        this.j = y;
        this.f = 0;
        this.g = 0;
        this.h = 0;
        this.neighbours = [];
        this.previous = null;
        if (Math.random() > wallRandomNess) {
            this.obstacle = false;
        } else {
            this.obstacle = true;
        }
    }

    addNeigbours = (Grid: any[], rows: number, cols: number) => {
        // Up
        if (this.i > 0) {
            this.neighbours.push(Grid[this.i - 1][this.j])
        }
        // Down
        if (this.i < (rows - 1)) {
            this.neighbours.push(Grid[this.i + 1][this.j])
        }
        // Left
        if (this.j > 0) {
            this.neighbours.push(Grid[this.i][this.j - 1])
        }
        // Right
        if (this.j < (cols - 1)) {
            this.neighbours.push(Grid[this.i][this.j + 1])
        }
        // Top-Left
        if (this.i > 0 && this.j > 0) {
            this.neighbours.push(Grid[this.i - 1][this.j - 1]);
        }
        // Bottom Left
        if (this.i < rows - 1 && this.j > 0) {
            this.neighbours.push(Grid[this.i + 1][this.j - 1]);
        }
        // TOp-Right
        if (this.i > 0 && this.j < cols - 1) {
            this.neighbours.push(Grid[this.i - 1][this.j + 1]);
        }
        // Bottom-Right
        if (this.i < rows - 1 && this.j < cols - 1) {
            this.neighbours.push(Grid[this.i + 1][this.j + 1]);
        }
    }

}

interface IState {
    rows: number,
    cols: number,
    gridSize: string,
    start: Node | null,
    end: Node | null,
    isSelectedCellStart: boolean,
    obstacleRandomNess: string,
    onlyPath: boolean
}

export default class PathFinder extends Component<{}, IState> {
    Grid: Array<Array<Node>>;
    OptimalPath: Array<Node>;

    constructor(props) {
        super(props)

        this.Grid = [];
        this.OptimalPath = [];

        this.state = {
            rows: 10,
            cols: 10,
            gridSize: "10",
            start: null,
            end: null,
            isSelectedCellStart: true,
            obstacleRandomNess: "0.2",
            onlyPath: false
        }
    }

    constructGrid = () => {

        const { rows, cols, obstacleRandomNess } = this.state;

        this.Grid = [];

        for (let i = 0; i < rows; i++) {
            let row = new Array();
            for (let j = 0; j < cols; j++) {
                row.push(new Node(i, j, parseFloat(obstacleRandomNess)));
            }
            this.Grid.push(row);
        }

        for (let i = 0; i < rows; i++) {
            for (let j = 0; j < cols; j++) {
                // console.log(i, j);
                this.Grid[i][j].addNeigbours(this.Grid, rows, cols);
            }
        }

        // console.log(this.Grid);

        this.setState({
            start: null,
            end: null,
            isSelectedCellStart: true
        });

        this.renderGrid();
    }

    animatePathConstruction = (optimalPath: Node[], openSet: (Node | null)[], closedSet: Node[]) => {
        const { onlyPath } = this.state;

        if (!onlyPath) {
            const openSetTimeFactor = 4000 / openSet.length;
            for (let i = 0; i < openSet.length; i++) {
                const cellId = openSet[i]!.i.toString() + "_" + openSet[i]!.j.toString();
                if (openSet[i] === this.state.end)
                    continue;
                setTimeout(() => {
                    document.getElementById(cellId)?.setAttribute("class", "grid-cell grid-cell-open");
                }, openSetTimeFactor * i);
            }

            const closedSetTimeFactor = 4000 / closedSet.length;
            for (let i = 1; i < closedSet.length; i++) {
                const cellId = closedSet[i].i.toString() + "_" + closedSet[i].j.toString();
                setTimeout(() => {
                    document.getElementById(cellId)?.setAttribute("class", "grid-cell grid-cell-closed");
                }, closedSetTimeFactor * i);
            }
        }

        for (let i = optimalPath.length - 2; i > 0; i--) {
            const cellId = optimalPath[i].i.toString() + "_" + optimalPath[i].j.toString();
            setTimeout(() => {
                document.getElementById(cellId)?.setAttribute("class", "grid-cell grid-cell-path");
            }, onlyPath ? (4000 / optimalPath.length) * (optimalPath.length - i) : 4000);
        }
    }

    computeHeuristic = (node: Node, endNode: Node) => {
        return Math.sqrt(Math.abs(node.i - endNode.i) + Math.abs(node.j - endNode.j));
    }

    computePath = (currentNode: Node) => {
        this.OptimalPath.push(currentNode);
        while (currentNode.previous !== null) {
            currentNode = currentNode.previous;
            this.OptimalPath.push(currentNode);
        }
    }

    solveUsingA_Star = () => {
        const { start, end } = this.state;

        if (start === null || end === null) {
            alert("What's your destiny?");
            return;
        }

        let openSet: Array<Node | null> = [];
        let closedSet: Array<Node> = [];
        this.OptimalPath = [];

        openSet.push(start);

        while (openSet.length > 0) {

            let lowestFScoreIdx = 0;

            for (let idx = 0; idx < openSet.length; idx++) {
                if (openSet[idx]!.f < openSet[lowestFScoreIdx]!.f) {
                    lowestFScoreIdx = idx;
                }
            }

            let currentCell: Node = openSet[lowestFScoreIdx]!;

            if (currentCell.i == end!.i && currentCell.j == end!.j) {
                this.computePath(currentCell);
                this.animatePathConstruction(this.OptimalPath, openSet, closedSet);
                return;
            }

            closedSet.push(currentCell);
            openSet.splice(lowestFScoreIdx, 1);

            for (let idx = 0; idx < currentCell.neighbours.length; idx++) {
                let neighbour = currentCell.neighbours[idx];

                if (!closedSet.includes(neighbour) && !neighbour.obstacle) {
                    let tentative_G_Score = neighbour.g + 1

                    if (openSet.includes(neighbour)) {
                        if (tentative_G_Score < neighbour.g) {
                            neighbour.g = tentative_G_Score;
                        }
                    }
                    else {
                        neighbour.g = tentative_G_Score;
                        openSet.push(neighbour);
                    }

                    neighbour.h = this.computeHeuristic(neighbour, end!);
                    neighbour.f = neighbour.g + neighbour.h;
                    neighbour.previous = currentCell;

                }
            }
        }
        alert("Ooops!! It seems like treasure is in the ocean.");
        return;
    }

    handleCellSelection = (e: any) => {

        const { start, end } = this.state;

        const val = e.target.id as string;
        const cellLocations = val.split('_');

        if (this.Grid[cellLocations[0]][cellLocations[1]].obstacle) {
            return;
        }

        if (this.state.isSelectedCellStart) {
            const prevCellId = start?.i + "_" + start?.j;
            document.getElementById(prevCellId)?.setAttribute("class", "grid-cell");

            this.setState({
                start: this.Grid[cellLocations[0]][cellLocations[1]],
                isSelectedCellStart: false
            }, () => {
                document.getElementById(val)?.setAttribute("class", "grid-cell grid-cell-start");
            });
        } else {
            const prevCellId = end?.i + "_" + end?.j;
            document.getElementById(prevCellId)?.setAttribute("class", "grid-cell");

            this.setState({
                end: this.Grid[cellLocations[0]][cellLocations[1]],
                isSelectedCellStart: true
            }, () => {
                document.getElementById(val)?.setAttribute("class", "grid-cell grid-cell-end");
            });
        }
    }

    renderGrid = () => {
        const { rows, cols, start, end } = this.state;

        let mainGrid = document.getElementById("grid");

        while (mainGrid?.lastChild) {
            mainGrid.lastChild.remove();
        }

        for (let i = 0; i < rows; i++) {
            let rowDiv = document.createElement("div");
            rowDiv.setAttribute("class", "grid-row");
            for (let j = 0; j < cols; j++) {
                let colDiv = document.createElement("div");
                if (this.Grid[i][j].obstacle) {
                    colDiv.setAttribute("class", "grid-cell grid-cell-wall");
                } else {
                    colDiv.setAttribute("class", "grid-cell");
                }
                colDiv.setAttribute("id", i.toString() + "_" + j.toString());
                colDiv.addEventListener("click", this.handleCellSelection);
                rowDiv.appendChild(colDiv);
            }
            mainGrid?.appendChild(rowDiv);
        }
    }

    componentDidMount() {
        this.constructGrid();
    }

    handleGridParamsChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const isMobile = window.innerWidth < window.innerHeight;
        if ((parseInt(e.target.value) < 0) ||
            (!isMobile && parseInt(e.target.value) > 50)
            || (isMobile && parseInt(e.target.value) > 10)
        ) {
            if (isMobile)
                alert("Screen can only have 10 cells max. (Try changing the orientation or open in laptop or desktop)")
            else
                alert("Max grid size reached !!")
            return;
        }

        const val = e.target.value;
        this.setState({
            gridSize: val,
            rows: parseInt(val),
            cols: parseInt(val),
        }, this.constructGrid);
    }

    render() {
        return (
            <div className="path-finder">
                <nav
                    className="navbar navbar-expand-lg fixed-top navbar-light shadow-lg rounded bg-light"
                >
                    <Link className="navbar-brand" to="/">
                        Prateek Jha
                    </Link>
                </nav>
                <div className="experiment jumbotron jumbotron-fluid bg-gradient-light m-0">
                    <div className="container container-fluid p-5">
                        <h1 className="display-4 pb-3">Maze</h1>
                        <div>
                            <div className="form-row">
                                <div className="col-md-3 mb-3"></div>
                                <div className="col-md-2 mb-3">
                                    <label htmlFor="">Grid Size</label>
                                    <input type="text" className="form-control" value={this.state.gridSize}
                                        onChange={this.handleGridParamsChange} placeholder="Number of grid cells" />
                                </div>
                                <div className="col-md-2 mb-3">
                                    <label htmlFor="">Obstalce Epsilon</label>
                                    <input type="text" className="form-control" value={this.state.obstacleRandomNess}
                                        onChange={(val) => this.setState({ obstacleRandomNess: val.target.value }, this.constructGrid)} placeholder="Obstactle smppling" />
                                </div>
                                <div className="col-md-2 mb-3">
                                    <label htmlFor="">Algorithm</label>
                                    <select className="form-control">
                                        <option>A-Star</option>
                                    </select>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col-md-4 mb-3"></div>
                                <div className="col-md-1 mb-3">
                                    <input className="form-check-input" type="checkbox" value={this.state.onlyPath.toString()}
                                        onChange={(val) => this.setState({ onlyPath: val.target.checked })}></input>
                                    <label className="form-check-label">Only Path</label>
                                </div>
                                <div className="col-md-1 mb-3">
                                    <button className="btn btn-light" onClick={this.solveUsingA_Star}>
                                        <i className="fa fa-play" aria-hidden="true"></i>
                                    </button>
                                </div>
                                <div className="col-md-1 mb-3">
                                    <button className="btn btn-light" onClick={this.constructGrid}>
                                        <i className="fa fa-repeat" aria-hidden="true"></i>
                                    </button>
                                </div>
                                <div className="col-md-1 mb-3">
                                    <button className="btn btn-light" data-toggle="modal" data-target="#exampleModalCenter">
                                        <i className="fa fa-info-circle" aria-hidden="true"></i>
                                    </button>
                                </div>
                            </div>
                        </div>
                        <div className="container pr-0 pl-0 mt-5">
                            <div className="grid center" id="grid">
                            </div>
                        </div>

                        <div className="modal fade" id="exampleModalCenter" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
                            <div className="modal-dialog modal-dialog-centered" role="document">
                                <div className="modal-content">
                                    <div className="modal-header">
                                        <h5 className="modal-title lead" id="exampleModalCenterTitle">A* Algorithm</h5>
                                        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                            <span aria-hidden="true">&times;</span>
                                        </button>
                                    </div>
                                    <div className="modal-body">
                                        <p className="text-justify">
                                            <b>A* (pronounced "A-star")</b> is a graph traversal and path search algorithm, which is often used in many fields of computer science due to its completeness, optimality, and optimal efficiency.
                                        One major practical drawback is its <b>O(b^d)</b> space complexity, as it stores all generated nodes in memory.
                                        Thus, in practical travel-routing systems, it is generally outperformed by algorithms which can pre-process the graph to attain better performance, as well as memory-bounded approaches;
                                        however, A* is still the best solution in many cases.
                                        <br /><br />
                                            <b>Peter Hart, Nils Nilsson</b> and <b>Bertram Raphael</b> of Stanford Research Institute (now SRI International) first published the algorithm in 1968.
                                        It can be seen as an extension of Edsger Dijkstra's 1959 algorithm. A* achieves better performance by using heuristics to guide its search.
                                    </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        )
    }
}
