import React, { Component } from 'react';
import './BlogCard.css'

interface IProps {
    blogUrl: string,
    description: string,
    title: string
}

export class BlogCard extends Component<IProps, {}> {
    constructor(props) {
        super(props)
    }

    findDomain(url: string) {
        let result: string = "";
        var match: RegExpMatchArray | null;
        if (match = url.match(/^(?:https?:\/\/)?(?:[^@\n]+@)?(?:www\.)?([^:\/\n\?\=]+)/im)) {
            result = match[1]
            if (match = result.match(/^[^\.]+\.(.+\..+)$/)) {
                result = match[1]
            }
        }
        return result;
    }

    render() {
        return (
            <div className="col col-md-8 offset-md-2">
                <div className="blog-card card" >
                    <div className="card-body">
                        <div className="title">
                            <h5 className="card-title">{this.props.title}</h5>
                            <a href={this.props.blogUrl} target="_blank">
                                <i className="fa fa-external-link-square" aria-hidden="true"></i>
                            </a>
                        </div>
                        <div className="card-text">
                            <p>
                                {this.props.description}
                            </p>
                        </div>
                        <h6>
                            Read full blog on <a href={this.props.blogUrl} target="_blank">{this.findDomain(this.props.blogUrl)}</a>
                        </h6>
                    </div>
                </div >
            </div >
        )
    }
}

export default BlogCard
